export default function DmgCelc() {

  // const [Main,setMain] = useState('')
  // const [Sprt, setSprt] = useState('')
  // const [Wepn,setWepn] = useState(U[Main].Wepn)
  // const [Armr, setArmr] = useState(U[Main].Armr)
  // const [Helm, setHelm] = useState(U[Main].Helm)
  // const [Acce, setAcce] = useState(U[Main].Acce)
  // const [Ench, setEnch] = useState(U[Main].Ench)

  const [MainKh, setMainKh] = useState([{ H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 }, { H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 }, { H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 }]);
  const [SprtKh, setSprtKh] = useState([{ H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 }, { H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 }, { H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 }]);
  const [MainSk, setMainSk] = useState([{ H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 },]);
  const [SprtSk, setSprtSk] = useState([{ H: 0, A: 0, D: 0, M: 0, R: 0, T: 0 },]);
  // const [ActSt, setActSt] = useState([U[Main].Act, U[Main].Act]);





  const [MainI, setMainI] = useState('')
  const [SprtI, setSprtI] = useState('')
  const [WepnI, setWepnI] = useState('')
  const [ArmrI, setArmrI] = useState('')
  const [HelmI, setHelmI] = useState('')
  const [AcceI, setAcceI] = useState('')
  const [EnchI, setEnchI] = useState('')





  const MainSlctOn = (e) => {//
    SlctMain = e
    console.log(e)
    MainKhCelc()
  }

  const MainKhCelc = () => {//

    setMainI(SlctMain)

    Wepn = U[SlctMain].Wepn
    setWepnI(Wepn)

    Armr = U[SlctMain].Armr
    setArmrI(Armr)

    Helm = U[SlctMain].Helm
    setHelmI(Helm)

    Acce = U[SlctMain].Acce
    setAcceI(Acce)

    Ench = U[SlctMain].Ench
    setEnchI(Ench)

    SlctSprt = U[SlctMain].Sprt.Nam
    setSprtI(SlctSprt)

    setMainKh(() => ([U[SlctMain].Gc, U[SlctMain].Ks, U[SlctMain].Kh]))






    MainSkCelc()
  }//MainKhCelc

  const EqSlctOn = (Main, Eq, EqNam) => {//
    console.log(Main)
    console.log(Eq)
    console.log(EqNam)

    U[Main][Eq] = EqNam
    SlctMain = Main

    MainSkCelc()
  }

  const EnchSlctOn = (Main, EnchNam) => {
    console.log(Main)
    console.log(EnchNam)
    U[Main].Ench = EnchNam
    SlctMain = Main

    MainSkCelc()
  }



  const MainSkCelc = () => {// 


    Object.keys(MainData).forEach(Main => {//=Main




      let Mlt = {}
      let Add = {}
      for (const K of HADMRT) {//
        Mlt[K] = 0//
        Add[K] = 0////+
      }

      for (const K of HADMRT) {

        if (U[Main].Wepn) Add[K] += WepnData[U[Main].Wepn][K]
        if (U[Main].Armr) Add[K] += ArmrData[U[Main].Armr][K]
        if (U[Main].Helm) Add[K] += HelmData[U[Main].Helm][K]
        if (U[Main].Acce) Add[K] += AcceData[U[Main].Acce][K]
        if (U[Main].Ench) Add[K] += EnchData[U[Main].Ench][K]


      }


      //

      for (const K of HADMRT) {//
        //Mlt[K]
        //Add[K] = C.Wepn[St] + C.Armr[St] + C.Helm[St] + C.Acce[St] + C.Ench[St]
        U[Main].Sk[K] = Math.floor(U[Main].Kh[K] * (1 + Mlt[K] / 100)) + Add[K]
      }
      setMainSk(() => ([U[Main].Sk]))

    })




    SprtSkCelc()
  }//MainSkCelc

  const SprtSlctOn = (Main, Sprt) => {//
    console.log(Main)
    console.log(Sprt)
    U[Main].Sprt.Nam = Sprt
    SlctMain = Main
    SlctSprt = Sprt
    SprtSkCelc()
  }


  const SprtSkCelc = () => {//



    let Mlt = {}
    let Add = {}
    for (const K of HADMRT) {//
      Mlt[K] = 0//
      Add[K] = 0////+
    }

    //console.log(U[SlctMain].Sprt)
    for (const K of HADMRT) {
      if (U[SlctSprt].AmTk) Add[K] += AmTkData[U[SlctSprt].AmTk][K]
      if (U[SlctSprt].AmTy) Add[K] += AmTyData[U[SlctSprt].AmTy][K]
    }

    //

    for (const K of HADMRT) {//
      //Mlt[K]
      //Add[K] = C.Wepn[St] + C.Armr[St] + C.Helm[St] + C.Acce[St] + C.Ench[St]
      U[SlctSprt].Sk[K] = Math.floor(U[SlctSprt].Kh[K] * (1 + Mlt[K] / 100)) + Add[K]


    }

    // console.log(U[SlctSprt].Gc)
    // U[SlctMain].Sprt.Gc = U[SlctSprt].Gc//
    // console.log(U[SlctMain].Sprt)


    // U[SlctMain].Sprt.Ks = U[SlctSprt].Ks//
    // U[SlctMain].Sprt.Kh = U[SlctSprt].Kh//
    // U[SlctMain].Sprt.Sk = U[SlctSprt].Sk//

    Celc()
  }

  const ActSlctOn = (Main, t, ActNam) => {
    U[Main].Act[t].Nam = ActNam
    SlctMain = Main

    MainSkCelc()
  }



  const Celc = () => {

    Object.keys(MainData).forEach(Main => {//
      let Sprt = U[Main].Sprt.Nam
      let MainSprt = [Main, Sprt]

      U[Main].Dmg = 0////
      U[Main].DmgCr = 0
      U[Main].DmgEx = 0
      U[Main].UnitDmg = 0
      U[Main].UnitDmgCr = 0
      U[Main].UnitDmgEx = 0
      U[Sprt].Dmg = 0

      for (let t of Object.keys(MainData[Main].ActPtt)) {//for
        let ActNam = U[Main].Act[t].Nam

        //
        for (const MS of MainSprt) {
          for (const AB of AlwBtl) {
            for (const K of StAll) {
              U[MS].Act[t][AB].Mlt[K] = 0
              U[MS].Act[t][AB].Add[K] = 0
            }
          }
          U[MS].Act[t].Num = 1
          U[MS].Act[t].Att = false

        }

        U[Main].Act[t].Alw.Mlt.Crd += 30
        U[Main].Act[t].CT = SklData[ActNam].CT
        U[Main].Act[t].Ara = SklData[ActNam].Ara
        U[Sprt].Act[t].Ara = ''
        U[Main].Act[t].Mag = SklData[ActNam].Mag

        if (ActNam === '') {//
          U[Main].Act[t].Rng = MainData[Main].Rng
        } else {//
          U[Main].Act[t].Rng = SklData[ActNam].Rng
        }
        U[Sprt].Act[t].Rng = SprtData[Sprt].Rng
        //


        ////
        switch (Main) {//
          case '':
            U[Main].Act[t].Alw.Mlt.A += 20
            U[Sprt].Act[t].Alw.Mlt.A += 20
            break
          case '':

            // 44-10%/-10%(1)4+10%/+10%(1)
            break
          case '':
            U[Main].Act[t].Alw.Mlt.A += 25
            U[Sprt].Act[t].Alw.Mlt.A += 25
            if (ActNam != '' && U[Main].Act[t].Rng >= 1) U[Main].Act[t].Rng += 1//HP75%,+1
            if (ActNam != '' && U[Main].Act[t].Ara >= 1) U[Main].Act[t].Ara += 1//HP75%,+1
            //+1//HP75%
            //+30%//HP75%
            break
          default:
            break
        }//
        switch (Sprt) {//
          case '':
            break
          default:
            break
        }//
        switch (U[Main].Wepn) {//
          case '':
            U[Main].Act[t].Alw.Mlt.A += 10
            break
          case '':
            U[Main].Act[t].Alw.Mlt.A += 10
            break
          default:
            break
        }//
        switch (U[Main].Armr) {//
          case '':
            U[Main].Act[t].Alw.Mlt.A += 10
            break
          case '':
            break
          default:
            break
        }
        switch (U[Main].Helm) {//
          case '':
            break
          default:
            break
        }
        switch (U[Main].Acce) {//
          case '':
            break

          default:
            break
        }
        switch (U[Main].Ench) {//4&2
          case '':
            if (ActNam !== '/') {
              U[Main].Act[t].Btl.Mlt.A += 15
              U[Main].Act[t].Btl.Mlt.DresA += 15
              U[Main].Act[t].Btl.Mlt.DresM += 15
            }
            break
          case '':
            U[Main].Act[t].Alw.Mlt.A += 10//HP70%
            U[Main].Act[t].Alw.Mlt.D += 10//HP70%
            U[Main].Act[t].Alw.Mlt.M += 10//HP70%
            U[Main].Act[t].Alw.Mlt.R += 10//HP70%
            break
          case '':
            U[Main].Act[t].Alw.Mlt.Damp += 10
            //40%+1(1)
            break
          case '':
            //50%CT-1
            break
          case '':
            if (ActNam[t] !== '' && ActNam[t] !== '/') {
              U[Main].Act[t].Alw.Mlt.Damp += 10
              if (U[Main].Act[t].Ara >= 1) U[Main].Act[t].Alw.Mlt.Damp += 10
            }
            break
          case '':
            U[Main].Act[t].Alw.Mlt.DresA += 15
            U[Main].Act[t].Alw.Mlt.DresM += 15
            break
          case '':
            break
          case '':
            U[Main].Act[t].Alw.Mlt.D += 5
            U[Main].Act[t].Alw.Mlt.R += 5
            break
          case '':
            break
          case '':
            break
          case '':
            break
          case '':
            U[Main].Act[t].Alw.Mlt.Crr += 7//2set
            U[Main].Act[t].Alw.Mlt.Crd += 30
            break
          case '':
            U[Main].Act[t].Alw.Mlt.Crr += 7//2set
            if (ActNam[t] !== '/') {
              U[Main].Act[t].Btl.Mlt.Damp += 20//HP50%
            }
            break
          default:
            break
        }
        switch (ActNam) {//=
          case '':
            //-10%1T
            break
          case '':
            //-10%1T
            break
          case '':
            U[Main].Act[t].Alw.Mlt.Crr += 50
            break

          case '':
            //if (C.Enmy.Cls === '') U[Main].Act[t].Mag = 2
            break
          case '':
            //U[Main].Act[t].Btl.MltMAct.Damp += 30
            //U[Main][Sprt].Act[t].Btl.MltMAct.Damp += 30
            break
          case '':
            //D/R-20%2T
            break

          default:
            break
        }





        ////
        for (const MS of MainSprt) {
          for (const K of StAll) {//
            U[MS].Act[t].Btl.Mlt[K] += U[MS].Act[t].Alw.Mlt[K]//+=
            U[MS].Act[t].Btl.Add[K] += U[MS].Act[t].Alw.Add[K]//+=

            //U[Main].Act[t].Alw.Mlt[K] += U[Main].Act[t].Alw.MltMAct[St]//+= 
            //C[MS].Act[t].Btl.Mlt[St] += Math.max(C[MS].Act[t].Alw.MltMAct[St], C[MS].Act[t].Btl.MltMAct[St])//+=max(,)

          }
          for (const K of HADMRT) {//
            U[MS].Act[t].Alw[K] = Math.floor(U[MS].Sk[K] * (1 + U[MS].Act[t].Alw.Mlt[K] / 100)) + U[MS].Act[t].Alw.Add[K]
            U[MS].Act[t].Btl[K] = Math.floor(U[MS].Sk[K] * (1 + U[MS].Act[t].Btl.Mlt[K] / 100)) + U[MS].Act[t].Btl.Add[K]
          }

        }
        U[Main].Act[t].Alw.Mlt.Crr += U[Main].Act[t].Alw.T / 10//
        U[Main].Act[t].Btl.Mlt.Crr += U[Main].Act[t].Btl.T / 10

        //
        let AB = ''
        if (U[Main].Act[t].Ara >= 1) AB = 'Alw'//Alw
        else AB = 'Btl'

        if (MainData[Main].A > MainData[Main].M) {//
          U[Main].Act[t].Dmg = (U[Main].Act[t][AB].A - 0)
        } else {//
          U[Main].Act[t].Dmg = (U[Main].Act[t][AB].M - 0)
        }
        if (SprtData[Sprt].A > SprtData[Sprt].M) {//
          U[Sprt].Act[t].Dmg = (U[Sprt].Act[t][AB].A - 0)
          U[Sprt].Act[t].Alw.M = null
          U[Sprt].Act[t].Btl.M = null
        } else {//
          U[Sprt].Act[t].Dmg = (U[Sprt].Act[t][AB].M - 0)
          U[Sprt].Act[t].Alw.A = null
          U[Sprt].Act[t].Btl.A = null
        }


        U[Main].Act[t].Dmg *= U[Main].Act[t].Mag
        U[Main].Act[t].DmgCr = U[Main].Act[t].Dmg * (1 + U[Main].Act[t][AB].Mlt.Crd / 100)

        U[Main].Act[t].Dmg = Math.floor(U[Main].Act[t].Dmg) * 10
        U[Sprt].Act[t].Dmg = Math.floor(U[Sprt].Act[t].Dmg) * 10
        U[Main].Act[t].DmgCr = Math.floor(U[Main].Act[t].DmgCr) * 10
        U[Main].Act[t].DmgEx = Math.floor((U[Main].Act[t].DmgCr * U[Main].Act[t][AB].Mlt.Crr / 100) + (U[Main].Act[t].Dmg * (1 - U[Main].Act[t][AB].Mlt.Crr / 100)))

        if (U[Main].Act[t].Ara >= 1) U[Sprt].Act[t].Dmg = null//null
        U[Main].Act[t].UnitDmg = U[Main].Act[t].Dmg + U[Sprt].Act[t].Dmg //1
        U[Main].Act[t].UnitDmgCr = U[Main].Act[t].DmgCr + U[Sprt].Act[t].Dmg
        U[Main].Act[t].UnitDmgEx = U[Main].Act[t].DmgEx + U[Sprt].Act[t].Dmg

        U[Main].Dmg += U[Main].Act[t].Dmg////
        U[Sprt].Dmg += U[Main].Act[t].Dmg
        U[Main].DmgCr += U[Main].Act[t].DmgCr
        U[Main].DmgEx += U[Main].Act[t].DmgEx
        U[Main].UnitDmg += U[Main].Act[t].UnitDmg//
        U[Main].UnitDmgCr += U[Main].Act[t].UnitDmgCr
        U[Main].UnitDmgEx += U[Main].Act[t].UnitDmgEx


      }//for

      //

      // setActSt(() => ([U[Main].Act, U[Main][Sprt].Act]))

      // U[Main].Sprt = {
      //   Nam: Sprt,
      //   Act: U[Sprt].Act
      // }

      U[Main].Sprt = U[Sprt]
      U[Main].Sprt.Nam = Sprt

    })//for


    console.log('U')
    console.log(U)


  }//Celc



  //<Stts.MainSk p={MainSk} />
  window.onload = MainKhCelc

  /*
  {<Select options={WepnList}
  defaultValue={{ label: WepnI, value: WepnI }} />
  <Slct.Main
  Op={MainList}
  onChange={MainSlctOn}
  />}
  */



  return (
    <div style={{ fontSize: 16 }}>
      <DataGrid_UnitDmg.UnitDmg
        U={U}
        MainList={MainList}
        MainSlctOn={MainSlctOn}

        SklData={SklData}


        WepnData={WepnData}
        ArmrData={ArmrData}
        HelmData={HelmData}
        AcceData={AcceData}
        EqSlctOn={EqSlctOn}

        EnchData={EnchData}
        EnchSlctOn={EnchSlctOn}

        SprtSlctOn={SprtSlctOn}
        SprtData={SprtData}

        ActSlctOn={ActSlctOn}


      />

    </div>

  )

}


