import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Button from "@material-ui/core/Button";



export function Ench(props) {

  const test = (EnchNam) => {
    props.handleClose()
    console.log(props.SlctMain)
    console.log(EnchNam)
    props.EnchSlctOn(props.SlctMain, EnchNam)
  }


  const columns = [
    { field: 'id',
     headerName: '',
      width: 40,
      headerAlign: 'center',
      align: 'center',
      renderCell: (params) => (
        <Button onClick={() => test(params.row.name)} >
          <img src={`${process.env.PUBLIC_URL}/${params.row.name}i.png`} style={{ width: 35 }} alt={''} />
        </Button>
      )
     },{
      field: 'name',
      headerName: '',
      width: 160,
    },
  ];
  

  const rows = []
  Object.keys(props.EnchData).forEach(EnchNam => {
      rows.push({
        id: EnchNam,
        name: EnchNam,
      })
  })



  return (
    <Box sx={{ height: 510, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        // pageSize={5}
        // rowsPerPageOptions={[5]}
        // checkboxSelection
        disableSelectionOnClick
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        autoHeight//
        showColumnRightBorder // 
        showCellRightBorder   // 
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//

      />
    </Box>
  );
}