import * as React from 'react';
import { DataGrid } from '@mui/x-data-grid';
import Button from '@mui/material/Button';




export function ActSlct(props) {//

  const test = (Main,t,ActNam) => {
    props.handleClose2()
    props.handleClose()
    props.ActSlctOn(Main,t,ActNam)
  }


  const columns = [
    // { field: 'id', headerName: 'ID', width: 70 },
  
    {
      field: 'icon',
      headerName: '',
      width: 40,
      type: 'string',
      headerAlign: 'center',
      align: 'center',
  
      renderCell: (params) => (
        <Button onClick={() => test(props.SlctMain,props.t, params.row.ActNam)}>
        <img src={`${process.env.PUBLIC_URL}/${params.row.ActNam}i.png`} style={{ width: 35 }} alt={''} />
        </Button>
  
      )
  
    }, {
      field: 'ActNam',
      headerName: '',
      width: 240,
      type: 'string',
      headerAlign: 'center',
  
    },
  
  
  
  
  
  
  ];
  








  const rows = []
  for (let i = 0; i < props.ActList.length; i++) {
    rows.push({
      id: i + 1,
      icon: props.ActList[i],
      ActNam: props.ActList[i],
    })
  }








  return (
    <div style={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        // pageSize={5}
        // rowsPerPageOptions={[5]}
        // checkboxSelection
        density='compact'//
        autoHeight//
        showColumnRightBorder // 
        showCellRightBorder   // 
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//
      />
    </div>
  );
}